* {
  margin: 0;
  padding: 0;
  font-family: nunito sans;
}

.back {
  width: auto;
  max-width: 100vw;
  max-height: 90vh;


}



.App {

  min-height: 100vh;
  position: relative;

}

.Home {
  min-height: 100vh;
  position: relative;
  padding: 0vh 10vw;
}

.home-box {
  border: 1px solid white;
  width: 21cm;
  padding: .25cm .45cm;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name {
  color: white;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 50px;
}

.tagline {
  color: white;
  font-family: acme;
  letter-spacing: .12cm;
  font-weight: 800;
  font-size: 70px;
}

.desc {
  color: white;
  font-family: acme;
  font-size: 40px;
  text-align: center;
}

.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("back.jpg");
  background-repeat: no-repeat;
  background-size: auto 100vh;
  background-position: center center;
  filter: brightness(35%);
  background-color: #3a4c7d;
}


@media only screen and (max-width:900px) {
  .Home::before {
    background-size: 95vw auto;
  }
}